.completed{
    text-decoration: line-through;
    color: lightslategray;
}
.todoApp{
    width: 35%;
    margin: auto;
}
.my-todo{
    margin: auto;
    width: 40%;
}
.complectedTodo{
    text-decoration: line-through;
    color: lightslategrey;
}
@media (max-width:768px){
    .todoApp, .my-todo{
        width: 100%;
    }
}

